<template>
  <div class="public">
    <header-content></header-content>
    <div class="public-content">
      <left :curid="curid" :isPull="isPull" @pull="pull" :visible="true" @change="changeSort"></left>
      <div class="right">
        <div class="right-content">
          <div class="title">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-shouye" />
            </svg>
            <p>
              你现在所在的位置是:
              <span>轮播图管理</span>
            </p>
          </div>
          <!-- 操作 -->
          <div class="operate">
            <el-button type="primary" icon="el-icon-success" class="sreach-btn" @click="delAll">批量删除</el-button>
          </div>
          <!-- 产品列表 -->
          <el-table
            class="pro-table"
            :data="tableData"
            border
            style="width: 100%"
            ref="poductTable"
            @selection-change="selectChange"
          >
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="id" label="编号" width="80"></el-table-column>
            <el-table-column prop="name" label="轮播图标题"></el-table-column>
            <el-table-column prop="state" sortable label="状态" width="120"></el-table-column>
            <el-table-column prop="classify" sortable label="分类" width="120"></el-table-column>
            <el-table-column label="图片" width="100">
              <template slot-scope="scope">
                <el-popover placement="top-start" title trigger="hover">
                  <img
                    :src="RespondURL.url+tableData[scope.$index].img"
                    alt
                    class="patent-img"
                    style="width: 250px;height: 250px"
                  />
                  <img
                    slot="reference"
                    :src="RespondURL.url+tableData[scope.$index].img"
                    style="width: 50px;height: 50px"
                  />
                </el-popover>
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <i class="el-icon-edit-outline" title="编辑" @click="edit(scope.$index)"></i>
                <i class="el-icon-delete" title="删除" @click="del(scope.$index)"></i>
              </template>
            </el-table-column>
          </el-table>

          <!-- 分页 -->
          <div class="page">
            <el-pagination
              layout="prev, pager, next"
              :total="total"
              @current-change="pageChange"
              @prev-click="pageChange"
              @next-click="pageChange"
              background
            ></el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import HeaderContent from "@/components/HeaderContent.vue";
import Left from "@/components/Left.vue";
export default {
  name: "Banner",
  components: { Left, HeaderContent },
  created() {
    if (this.$route.query.id) {
      this.getList(1, this.$route.query.id);
      this.classify = this.$route.query.id;
    }
  },
  data() {
    return {
      classify: "1",
      curid: "6",
      stateArr: ["下架", "上架"], // 状态
      productArr: ["智慧阅读", "智能穿戴", "数据库"], // 状态
      total: 0, // 总个数
      tableData: [], //列表数据
      checked: [], //被选中的数据
      isPull: false
    };
  },
  methods: {
    // 下拉
    pull() {
      if (this.isPull) {
        this.isPull = false;
      } else {
        this.isPull = true;
      }
    },
    changeSort(data) {
      this.getList(1, data);
      this.classify = data;
    },
    // 获得列表
    getList(sum, index) {
      let url = "";
      if (index == 2) {
        url = this.RespondURL.url + "GoodsCarousel/findAll/" + sum;
      } else {
        url = this.RespondURL.url + "Slideshow/selectslideshow/" + sum;
      }
      this.$ajax.get(url).then(data => {
        data.data.result.list.forEach(val => {
          let num = parseInt(val.state);
          val.state = this.stateArr[num];
        });
        this.$store.commit("setArr", data.data.result.list);
        if (index == 2) {
          data.data.result.list.forEach(val => {
            let num = parseInt(val.classify);
            val.classify = this.productArr[num - 1];
          });
        }
        this.tableData = data.data.result.list;
        this.total = data.data.result.total;
        console.log( data  )
      });
    },
    // 选中
    selectChange(data) {
      console.log(data);
      this.checked = data;
    },
    //编辑
    edit(index) {
      this.$router.push({
        path: "/bannerAdd",
        query: {
          id: index,
          classify: this.classify
        }
      });
    },
    // 删除
    del(index) {
      let obj = {
        id: [this.tableData[index].id]
      };
      this.$confirm("此操作将删除这些轮播图, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          let url = "";
          if (this.classify == 2) {
            // 产品轮播
            url = this.RespondURL.url + "GoodsCarousel/deleteslideshow";
          } else {
            url = this.RespondURL.url + "Slideshow/deleteslideshow";
          }
          this.$ajax.post(url, obj).then(data => {
            if (data.data.flag) {
              this.tableData.splice(index, 1);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 批量删除
    delAll() {
      if (this.checked.length == 0) {
        return;
      }
      let arr = this.tableData.map(x => x);
      let idArr = [];
      let imgArr = [];
      this.checked.forEach(val => {
        idArr.push(val.id);
        imgArr.push(val.img);
        arr.forEach((value, index) => {
          if (val.id == value.id) {
            arr.splice(index, 1);
          }
        });
      });
      let obj = {
        id: idArr
      };
      this.$confirm("此操作将删除这些轮播图, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          //删除活动
          let url = "";
          if (this.classify == 2) {
            url = this.RespondURL.url + "GoodsCarousel/deleteslideshow";
          } else {
            url = this.RespondURL.url + "Slideshow/deleteslideshow";
          }
          this.$ajax.post(url, obj).then(data => {
            if (data.data.flag) {
              this.tableData = arr;
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 分页
    pageChange(data) {
      this.getList(data, this.classify);
    }
  }
};
</script>
